<template>
  <div class="InfoDynamicTable">
    <el-row>
      <!--  -->
      <el-col :span="24">
        <div class="ModuleTit TextAlignL"></div>
        <el-form ref="formBasic" :model="formBasic" label-width="90px" size="small" class="DisabledNormal MarginT_20"  style="padding-right: 20px;">
          <el-row>
            <el-col :span="10">
              <el-form-item label="">
                <el-input v-model="formBasic.contractNo" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="14">
              <el-form-item label="">
                <el-input v-model="formBasic.projectName" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <el-form-item label="">
                <el-input v-model="formBasic.signDepartment" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-input v-model="formBasic.customerName" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-input v-model="formBasic.qyrq" disabled></el-input>
              </el-form-item>
            </el-col>
            <!-- <el-col :span="8">
              <el-form-item label="">
                <el-input v-model="formBasic.isSigned" disabled></el-input>
              </el-form-item>
            </el-col> -->
          </el-row>
          <el-row>
            <el-col :span="6" class="TextAlignL">
              <el-form-item label="">
                <el-input v-model="formBasic.swry" clearable style="width:70%;"></el-input>
                <el-popover
                  placement="right"
                  width="100"
                  trigger="click">
                  <div>
                    <p v-for="(people, idx) in swryHistory" :key="idx">{{people['']}}</p>
                  </div>
                  <el-button type="text" style="width:25%;display: inline-block;" slot="reference"></el-button>
                </el-popover>
              </el-form-item>
            </el-col>
            <el-col :span="6" class="TextAlignL">
              <el-form-item label="">
                <el-input v-model="formBasic.xmjl" clearable style="width:70%;"></el-input>
                <el-popover
                  placement="right"
                  width="100"
                  trigger="click">
                  <div>
                    <p v-for="(people, idx) in xmjlHistory" :key="idx">{{people['']}}</p>
                  </div>
                  <el-button type="text" style="width:25%;display: inline-block;" slot="reference"></el-button>
                </el-popover>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.htje" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.ysmll" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="">
                <el-date-picker
                  v-model="formBasic.kgrq"
                  type="date"
                  value-format="yyyy-MM-dd"
                  placeholder="">
                </el-date-picker>
                <!-- <el-input v-model="formBasic.kgrq"></el-input> -->
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-date-picker
                  v-model="formBasic.wgrq"
                  type="date"
                  value-format="yyyy-MM-dd"
                  placeholder="">
                </el-date-picker>
                <!-- <el-input v-model="formBasic.wgrq"></el-input> -->
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.jsj" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.sjj" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.ljkp" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.ljsk" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.chl" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.skl" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.jgrq" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.zbq" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.zbks" disabled></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="">
                <el-input v-model="formBasic.zbjs" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-button class="MarginB_20" type="danger" @click="save"> </el-button>
        </el-form>
      </el-col>
      <!--  -->
      <el-col :span="24">
        <div class="ModuleTit TextAlignL"></div>
        <section v-if="receiptRateInfo.length > 0" class="TextAlignL MarginB_10" style="font-size: 14px;height: 150px;overflow-y: scroll;padding:0 5px 5px 20px;">
          <el-row style="margin-bottom: 10px; font-weight: bold;">
            <el-col :span="4"></el-col>
            <el-col :span="4"></el-col>
            <el-col :span="4"></el-col>
            <el-col :span="4"></el-col>
            <el-col :span="4"></el-col>
          </el-row>
          <div v-for="(item, idx) in receiptRateInfo" :key="idx" class="text item MarginB_10">
            <el-row>
              <el-col :span="4">{{idx + 1}}. {{item.FName}}</el-col>
              <el-col :span="4">{{item.fpercent}}</el-col>
              <el-col :span="4">{{item.ys}}</el-col>
              <el-col :span="4">{{item.sj}}</el-col>
              <el-col :span="4">{{item.ws}}</el-col>
            </el-row>
            <!-- <span>{{idx + 1}}. {{item.FName}}</span>
            <span style="margin-left: 100px;">{{item.fpercent}}</span> -->
          </div>
        </section>
        <!--  -->
        <section v-else class="TextAlignL MarginB_10" style="font-size: 14px;height: 100px;">
          <div style="text-align: center;line-height: 100px;" ></div>
        </section>
      </el-col>
      <!--  -->
      <!-- <el-col :span="12">
        <div class="ModuleTit TextAlignL"></div>
        <section class="TextAlignL" style="font-size: 14px;height: 140px;overflow-y: scroll;">
          <div v-for="(item, idx) in warnList" :key="idx" class="text item" style="padding: 5px 5px 5px 20px;">
            <span>{{idx + 1}}. {{item}}</span>
          </div>
        </section>
      </el-col> -->
    </el-row>
    <!--  -->
    <!-- 1023(),  -->
    <el-col :span="24" class="TextAlignL"><span class="ModuleTit"></span></el-col>
    <el-col :span="24" class="StepWrap TextAlignL" style="width: 100%;overflow-x: scroll;">
      <div :class="{SetpItem: true, 'NotAllowed': item.status == 0}" v-for="(item, idx) in steps" :key="idx" @click="changeStep(idx)">
        <div :class="{'LineItem':true, 'bgGrey': item.status == 0, 'bgYellow': item.status == 1 && curLuiCheng != item.tit, 'bgBlue': item.status == 1 && curLuiCheng == item.tit, 'bgGreen': item.status == 2, 'bgRed': item.status == 3}" v-show="idx > 0"></div>
        <div class="DotItemwrap">
          <div class="TextItem">
            <p>{{item.tit == '' || item.tit == '' ? formBasic.swry : formBasic.xmjl}}</p>
          </div>
          <div :class="{'DotItem':true, 'bgGrey': item.status == 0, 'bgYellow': item.status == 1 && curLuiCheng != item.tit, 'bgBlue': item.status == 1 && curLuiCheng == item.tit, 'bgGreen': item.status == 2, 'bgRed': item.status == 3}"></div>
          <div class="TextItem">
            <p>{{item.tit}}{{item.author}}</p>
          </div>
        </div>
      </div>
    </el-col>
    <!-- ProcessEdit -->
    <ProcessEdit v-if="ifEdit" :curTimeStamp="curTimeStamp" :processStatus="processStatus" :processName="processName" :curEditIdx="curEditIdx" :curLuiChengIdx="curLuiChengIdx" :contractNo="formBasic.contractNo" @markRed="markRed" @refresh="getInfor" @toggleProcessDialog="toggleProcessDialog"/>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import $ from 'jquery'
import ProcessEdit from '../components/ProcessEdit'
export default {
  name: 'InfoDynamicTable',
  data () {
    return {
      ifEdit: false,
      processName: '',
      curLuiCheng: '',
      curLuiChengIdx: '',
      curEditIdx: '',
      processStatus: '',
      formBasic: {},
      xmjlHistory: [],
      swryHistory: [],
      receiptRateInfo: [],
      warnList: [''],
      steps: [],
      curTimeStamp: '',
      liuCheng: ['', '', '', '', '', '', '', '', '', '', '']
    }
  },
  components: {
    ProcessEdit
  },
  computed: {
    ...mapState({
      curContractNo: state => state.curContractNo,
      userInfo: state => state.userInfo
    })
  },
  created () {
    this.getInfor()
    this.getxmjl()
    this.getSwry()
  },
  methods: {
    toggleProcessDialog (val) {
      this.ifEdit = val
    },
    // 
    save () {
      let data = {
        items: [
          {
            FContractNo: this.curContractNo,
            f1: this.formBasic.swry,
            f2: this.formBasic.xmjl,
            fsdate: this.formBasic.kgrq ? this.formBasic.kgrq : '',
            fedate: this.formBasic.wgrq ? this.formBasic.wgrq : ''
          }
        ]
      }
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<Contractdetail xmlns="http://tempuri.org/">'
      tmpData += '<FJSONMSG>' + JSON.stringify(data) + '</FJSONMSG>'
      tmpData += '</Contractdetail>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'
      this.Http.post('Contractdetail', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 
        let Result = xmlDoc.getElementsByTagName('ContractdetailResponse')[0].getElementsByTagName('ContractdetailResult')[0]
        let HtmlStr = $(Result).html()
        let Info = (JSON.parse(HtmlStr))[0]
        if (Info.code === '1') {
          this.$message({
            message: '!',
            type: 'success'
          })
          this.getInfor()
          this.getxmjl()
          this.getSwry()
        } else {
          this.$message({
            message: '!',
            type: 'error'
          })
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    changeStep (idx) {
      this.processStatus = this.steps[idx].status
      this.curTimeStamp = new Date().getTime()
      this.processName = this.steps[idx].tit
      switch (this.steps[idx].status) {
        case 0:
          this.$message({
            message: '',
            type: 'warning'
          })
          break
        // case 2:
        //   this.toggleProcessDialog(true)
        //   break
        default:
          if (!this.steps[idx].author) {
            this.$message({
              message: '',
              type: 'warning'
            })
          } else {
            this.curEditIdx = idx
            this.toggleProcessDialog(true)
          }
          // if (this.curLuiCheng !== this.steps[idx].tit) {
          //   this.$message({
          //     message: '',
          //     type: 'warning'
          //   })
          // } else {
          //   this.toggleProcessDialog(true)
          // }
      }
    },
    async getInfor () {
      let ReceiptRate = await this.getReceiptRate()
      let Author = await this.getAuthor()
      this.getBasicInfor(ReceiptRate, Author)
    },
    getBasicInfor (ReceiptRate, Author) {
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
      tmpData += "<FSQL>SELECT * FROM Z_Contract WHERE  ='" + this.curContractNo + "'</FSQL>"
      tmpData += '</JA_LIST>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'

      this.Http.post('JA_LIST', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 
        let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
        let HtmlStr = $(Result).html()
        let Info = (JSON.parse(HtmlStr))[0]
        this.formBasic = {
          contractNo: Info[''],
          projectName: Info[''],
          signDepartment: Info[''],
          customerName: Info[''],
          // isSigned: Info[''],
          swry: Info[''],
          qyrq: Info[''],
          htje: Info[''],
          ysmll: '',
          xmjl: Info[''],
          kgrq: Info[''] ? Info[''].slice(0, 10) : '',
          wgrq: Info[''] ? Info[''].slice(0, 10) : '',
          jsj: Info[''],
          sjj: Info[''],
          ljkp: Info[''],
          ljsk: Info[''],
          chl: Info[''],
          skl: Info[''],
          jgrq: Info[''],
          zbq: Info[''],
          zbks: Info[''],
          zbjs: Info['']
        }
        this.steps = [
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']},
          {status: Info[''], date: '', tit: '', author: Author['']}
        ]
        for (let i = 0; i < 11; i++) {
          if (Info[this.liuCheng[i]] === 1) {
            this.curLuiCheng = this.liuCheng[i] === '' ? '--' : this.liuCheng[i]
            this.curLuiChengIdx = i
            break
          }
        }
        this.receiptRateInfo = ReceiptRate.map(item => {
          let percent = item.fpercent.substr(0, item.fpercent.length - 1) / 100
          item.ys = this.formBasic.htje * percent
          item.sj = this.formBasic.ljsk * percent
          item.ws = item.ys - item.sj
          return item
        })
      }).catch((error) => {
        console.log(error)
      })
    },
    getSwry () {
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
      tmpData += "<FSQL><![CDATA[select f1  from Z_Contract_log where isnull(f1,'')<>'' and FContractNo='" + this.curContractNo + "'  order by fid]]></FSQL>"
      tmpData += '</JA_LIST>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'

      this.Http.post('JA_LIST', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 
        let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
        let HtmlStr = $(Result).html()
        let Info = (JSON.parse(HtmlStr))
        this.swryHistory = Info
      }).catch((error) => {
        console.log(error)
      })
    },
    getxmjl () {
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
      tmpData += "<FSQL><![CDATA[select f2  from Z_Contract_log where isnull(f2,'')<>'' and FContractNo='" + this.curContractNo + "' order by fid]]></FSQL>"
      tmpData += '</JA_LIST>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'

      this.Http.post('JA_LIST', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 
        let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
        let HtmlStr = $(Result).html()
        let Info = (JSON.parse(HtmlStr))
        this.xmjlHistory = Info
      }).catch((error) => {
        console.log(error)
      })
    },
    getReceiptRate () {
      return new Promise((resolve, reject) => {
        var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
        tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
        tmpData += '<soap:Body> '
        tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
        tmpData += "<FSQL>select c.FName,(convert(varchar(50),b.FInteger)+'%')fpercent from t_RPContract a inner join skbl b on a.FContractID=b.FContractID inner join (select FInterID,FName from t_SubMessage where FTypeID=10001) c on c.FInterID=b.FBase1 where a.FContractNo='" + this.curContractNo + "'</FSQL>"
        tmpData += '</JA_LIST>'
        tmpData += '</soap:Body>'
        tmpData += '</soap:Envelope>'

        this.Http.post('JA_LIST', tmpData
        ).then(res => {
          let xml = res.data
          let parser = new DOMParser()
          let xmlDoc = parser.parseFromString(xml, 'text/xml')
          // 
          let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
          let HtmlStr = $(Result).html()
          let Info = (JSON.parse(HtmlStr))
          resolve(Info)
        }).catch((error) => {
          console.log(error)
        })
      })
    },
    getAuthor () {
      return new Promise((resolve, reject) => {
        var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
        tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
        tmpData += '<soap:Body> '
        tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
        tmpData += '<FSQL>select isnull(f_102,0),isnull(f_103,0),isnull(f_104,0),isnull(f_105,0),isnull(f_106,0),isnull(f_107,0),isnull(f_108,0),isnull(f_109,0),isnull(f_110,0),isnull(f_111,0),isnull(f_112,0) from t_emp where fitemid=' + this.userInfo.fempid + '</FSQL>'
        tmpData += '</JA_LIST>'
        tmpData += '</soap:Body>'
        tmpData += '</soap:Envelope>'

        this.Http.post('JA_LIST', tmpData
        ).then(res => {
          let xml = res.data
          let parser = new DOMParser()
          let xmlDoc = parser.parseFromString(xml, 'text/xml')
          // 
          let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
          let HtmlStr = $(Result).html()
          let Info = (JSON.parse(HtmlStr))[0]
          resolve(Info)
        }).catch((error) => {
          console.log(error)
        })
      })
    },
    // 
    markRed (Idx) {
      this.steps.map((step, idx) => {
        if (step.status === 1 && idx < Idx) {
          var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
          tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
          tmpData += '<soap:Body> '
          tmpData += '<jindu xmlns="http://tempuri.org/">'
          tmpData += '<FContractNo>' + this.curContractNo + '</FContractNo>'
          tmpData += '<FType>' + step.tit + '</FType>'
          tmpData += '<FColor>' + 3 + '</FColor>'
          tmpData += '</jindu>'
          tmpData += '</soap:Body>'
          tmpData += '</soap:Envelope>'
          this.Http.post('jindu', tmpData
          ).then(res => {
            // let xml = res.data
            // let parser = new DOMParser()
            // let xmlDoc = parser.parseFromString(xml, 'text/xml')
            // // 
            // let Result = xmlDoc.getElementsByTagName('jinduResponse')[0].getElementsByTagName('jinduResult')[0]
            // let HtmlStr = $(Result).html()
            // let Info = (JSON.parse(HtmlStr))[0]
            // if (Info.code === '1') {
            //   // this.$message({
            //   //   message: '!',
            //   //   type: 'success'
            //   // })
            //   // this.refresh()
            // } else {
            //   // this.$message({
            //   //   message: '!',
            //   //   type: 'error'
            //   // })
            // }
          }).catch((error) => {
            console.log(error)
          })
        }
        if (idx === 10) {
          this.getInfor()
        }
      })
    }
  }
}
</script>

<style lang='less' scoped>
@stepItemWidth: 150px;
.mainContainer {
  display:flex;
  display: -webkit-flex;
  flex-direction: row;
  -webkit-flex-direction: row;
  -webkit-align-content: stretch;
  align-content: stretch;
}
.topLeft {
  width:70%;
  // background-color:lightgreen;
}
.topRight {
  // background-color: red;
  -webkit-flex: 1;
  flex:1;
  position:relative;
}
.ModuleTit{
  width: calc(100% - 5px);
  height: 42px;
  background: #ddd;
  padding-left: 5px;
  display: block;
  line-height: 42px;
  font-weight: bold;
  margin-bottom: 10px;
}
.StepWrap{
  display: flex;
  align-items: center;
  padding-bottom: 20px;
}
.SetpItem{
  width: @stepItemWidth;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  cursor: pointer;
  .DotItemwrap{
    width: @stepItemWidth/2;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    .DotItem{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      margin-top: 15px;
      margin-bottom: 10px;
    }
  }
  .LineItem{
    width: @stepItemWidth/2;
    height: 2px;
    float: left;
  }
}
.TextItem{
  font-size: 12px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}
.bgGreen{
  background: green;
}
.bgYellow{
  background: yellow;
}
.bgBlue{
  background: blue;
}
.bgRed{
  background: red;
}
.bgGrey{
  background: grey;
}

</style>



