import axios from 'axios'
import { Message } from 'element-ui'
import utils from './utils.js'
import store from '../vuex/store'

// let wsdlurl = '/api/Service1.asmx'
let wsdlurl = 'http://192.168.1.9:8090/Service1.asmx'
//let wsdlurl = 'http://111.231.134.126:8090/Service1.asmx'

const CancelToken = axios.CancelToken
const source = CancelToken.source()
// token
function setToken () {
  if (utils.local.get('token')) {
    axios.defaults.headers.common['token'] = utils.getLocal('token')
  }
}

// err
const err = (error) => {
  if (error.response) {
    let data = error.response.data
    store.dispatch('toggleLoadingBt', false)
    // const token = Vue.ls.get(ACCESS_TOKEN)
    // console.log("------------",token)
    // console.log("------------",error.response.status)
    switch (error.response.status) {
      case 403:
        Message.error({
          message: '!'
        })
        break
      case 500:
        if (data.message === 'Token') {
          Message.error({
            message: '!'
          })
        }
        break
      case 404:
        Message.error({
          message: '!'
        })
        break
      case 504:
        Message.error({
          message: '!'
        })
        break
      case 401:
        Message.error({
          message: '!'
        })
        // if (token) {
        //   store.dispatch('Logout').then(() => {
        //     setTimeout(() => {
        //       window.location.reload()
        //     }, 1500)
        //   })
        // }
        break
      default:
        Message.error({
          message: data.message + '!'
        })
        break
    }
  }
  return Promise.reject(error)
}

// 
axios.interceptors.request.use(config => {
  // store.commit('UPDATE_LOADING', true) // loading,vuex
  // // ,,()
  // if (config.url.indexOf('login') < 0) {
  //   let lessTime = Number(uitls.local.get('expireTime')) - Date.now(); // 
  //   if (lessTime <= 0) {
  //     Message.error({
  //       content: '',
  //       duration: 5
  //     });
  //     source.token('token');
  //     store.commit('UPDATE_LOADING', false); // loading
  //     window.location.href = '/login';
  //     utils.local.clear();
  //   }
  // }
  return config
})

// 
axios.interceptors.response.use(res => {
  // if (res.config.url.indexOf('login') >= 0) {
  //   const { token, expireTime, code, data } = res.data;
  //   if (code == '200') {
  //     // ,()
  //     //...................
  //     utils.local.set('expireTime', expireTime); // 
  //     utils.local.set('token', token);
  //   }
  // }
  return res
}, err)

// 
function formatReq (type, SOAPAction, data) {
  const timestamp = Date.now()
  setToken()
  return new Promise((resolve, reject) => {
    axios({
      method: type,
      url: `${wsdlurl}`,
      crossDomain: true,
      headers: {
        // 
        // 'content-Type': 'application/x-www-form-urlencoded'
        // 'content-Type': 'application/json',
        'content-Type': 'text/xml; charset=utf-8',
        'SOAPAction': 'http://tempuri.org/' + SOAPAction,
        'X-Timestamp': timestamp
      },
      cancelToken: source.token,
      data: data // javaqs qs.stringify(data)
      // data:JSON.stringify(data)//PHPJSON
    })
      .then(r => {
        // store.commit('UPDATE_LOADING', false) // loading
        // ,,res.data.code === '6666' login
        resolve(r)
      })
      .catch(e => {
        // store.commit('UPDATE_LOADING', false) // loading
        reject(e.message)
        // Message.error(e.message)
      })
  })
}

const Http = {
  post: (SOAPAction, data) => formatReq('post', SOAPAction, data)
}

export default Http



