                    <el-form-item label="()" label-width="150px">
                      <el-input v-model="formFilter.receivablesRate"></el-input>
                    </el-form-item>
                    <el-form-item label="" label-width="110px">
                      <el-select v-model="formFilter.inQuality" placeholder="">
