                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span="8" :offset="1">
                    <section>
                      <p style="text-align: left;"></p>
                      <div class="sortBlock">
                        <el-menu
                          default-active=""
                          class="el-menu-vertical-demo">
                          <el-menu-item v-for="(item, idx) in sortList" :key="idx" :index="item">
                            <span slot="title">{{item}}</span>
                          </el-menu-item>
                        </el-menu>
                      </div>
                    </section>
                    <section>
                      <p style="text-align: left;padding-top: 5px;"></p>
                      <div class="vagueSearchBlock">
                        <!-- @change="mohuSearch" @keyup.enter.native='enterEvent' -->
                        <el-input class="MarginT_10" @blur="mohuSearch" @keyup.enter.native='enterEvent'  v-model="formFilter.vagueSearch" clearable size="mini"></el-input>
                        <P style='font-size: 12px;margin-top: 5px;padding: 0 5px;'>**</P>
                      </div>
