                  </el-col>
                </el-row>
              </el-col>
            </el-form>
          </el-row>
        </section>
      </el-col>
      <el-col :span="13" id="ResultBlock" class="ResultBlock BgGray">
        <el-table id="resultTable"
          v-loading="loading"
          :data="resultData"
          :height="tableHieght"
          border
          @row-dblclick="goDetail"
          style="width: 100%">
          <el-table-column
