          </el-table-column>
          <el-table-column
            prop="budgetGrossProfit"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="noSignNo"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="classManage"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="process"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="noCompleted"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="shipmentRate"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="receivablesRate"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="completedDate"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="guaranteePeriod"
            label="()"
            width="180">
          </el-table-column>
          <el-table-column
            prop="guaranteeStart"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="guaranteeEnd"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="signDepartment"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="businessmen"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="projectManager"
            label=""
            width="180">
          </el-table-column>
          <el-table-column
            prop="note"
            label=""
            width="180">
          </el-table-column> -->
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import $ from 'jquery'
export default {
  name: 'FilterTable',
  data () {
    return {
      loading: false,
      tableHieght: 0,
      formFilter: {
        signDepartment: -1,
        contractPrice: -1,
        signYear: -1,
        warnTip: -1,
        signAndFinish: -1,
        fileConditionn: -1,
        classCondition: -1,
        receivablesContion: -1,
        performBond: -1,
        qualityBond: -1,
        shipmentRate: '',
        receivablesRate: '',
        inQuality: -1,
        subItems: -1,
        sort: '',
        vagueSearch: ''
      },
      resultData: [],
      signDepartmentList: [
        {label: '', value: -1},
        {label: '', value: 0},
        {label: '', value: 1},
        {label: '', value: 2},
        {label: '', value: 3},
        {label: '', value: 4},
        {label: '', value: 5}
      ],
      contractPriceList: [
        {label: '', value: -1},
        {label: '99', value: 0},
        {label: '100-999', value: 1},
        {label: '1000-2999', value: 2},
        {label: '3000', value: 3}
      ],
      signYearList: [
        {label: '', value: -1},
        {label: '2015', value: 0},
        {label: '2016', value: 1},
        {label: '2017', value: 2},
        {label: '2018', value: 3},
        {label: '2019', value: 4}
      ],
      warnTipList: [
        {label: '', value: -1},
        {label: '', value: 0}
      ],
      signAndFinishList: [
        {label: '', value: -1},
        {label: '', value: 0},
        {label: '', value: 1},
        {label: '', value: 2}
      ],
      fileConditionnList: [
        {label: '', value: -1},
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      classConditionList: [
        {label: '', value: -1},
        {label: '', value: 0},
        {label: '', value: 1},
        {label: '', value: 2}
      ],
      receivablesContionList: [
        {label: '', value: -1},
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      performBondList: [
        {label: '', value: -1},
        {label: '', value: 0}
      ],
      qualityBondList: [
        {label: '', value: -1},
        {label: '', value: 0}
      ],
      shipmentRateList: [],
      inQualityList: [
        {label: '', value: -1},
        {label: '', value: 0},
        {label: '', value: 1}
      ],
      subItemsList: [
        {label: '', value: -1},
        {label: '', value: 0}
      ],
      sortList: ['', '', '', '', '', '', '', '']
    }
  },
  created () {
    setTimeout(() => {
      let height = document.documentElement.clientHeight
      document.getElementById('FilterBlock').style.height = height + 'px'
      this.tableHieght = height
    }, 0)
    this.getList()
    // if (this.resultDataOrigin.length > 0) {
    //   this.getList()
    // }
  },
  computed: {
    ...mapState({
      resultDataOrigin: state => state.resultDataOrigin
    })
  },
  methods: {
    ...mapActions([
      'updateContractNo',
      'updateResultData'
    ]),
    goDetail (row) {
      this.updateContractNo(row[''])
      this.$router.push({name: 'InfoDynamicTable'})
    },
    getList () {
      var tmpData = '<?xml version="1.0" encoding="utf-8"?>'
      tmpData += '<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/" xmlns:xsi="http://www.w3.org/2001/XMLSchema-instance" xmlns:xsd="http://www.w3.org/2001/XMLSchema"> '
      tmpData += '<soap:Body> '
      tmpData += '<JA_LIST xmlns="http://tempuri.org/">'
      tmpData += '<FSQL>select FContractNo ,b.fname ,b.FNumber  from t_RPContract a left join t_Item_3001 b on a.fbase=b.FItemID order by FContractNo</FSQL>'
      tmpData += '</JA_LIST>'
      tmpData += '</soap:Body>'
      tmpData += '</soap:Envelope>'

      this.Http.post('JA_LIST', tmpData
      ).then(res => {
        let xml = res.data
        let parser = new DOMParser()
        let xmlDoc = parser.parseFromString(xml, 'text/xml')
        // 
        let Result = xmlDoc.getElementsByTagName('JA_LISTResponse')[0].getElementsByTagName('JA_LISTResult')[0]
        let HtmlStr = $(Result).html()
        // let Info = JSON.parse(HtmlStr)
        // Info.map((item, idx) => {
        //   let obj = {
        //     contractNo: item[''],
        //     projectName: item[''],
        //     projectCode: item['']
        //   }
        //   this.resultData.push(obj)
        //   if (idx + 1 === Info.length) {
        //     this.loading = false
        //   }
        // })
        this.updateResultData(JSON.parse(HtmlStr))
        // this.resultData = JSON.parse(HtmlStr)
        // this.loading = false
      }).catch((error) => {
        console.log(error)
      })
    },
    enterEvent () {
      let keyCode = event.keyCode ? event.keyCode : event.which ? event.which : event.charCode
      if (keyCode === 13) {
        this.mohuSearch()
      }
    },
    // 
    mohuSearch () {
      if (this.formFilter.vagueSearch) {
        this.loading = true
        let newResult = []
        let keyArr = this.formFilter.vagueSearch.trim().split('*')
        var regStr = ''
        keyArr.map(function (item) {
          regStr = regStr + '.*' + item
          // regStr = regStr + '\\w*' + item
        })
        var reg = new RegExp(regStr)
        this.resultDataOrigin.map((item, idx) => {
          if (reg.test(item[''])) {
            newResult.push(item)
          }
        })
        this.resultData = newResult
        this.loading = false
      } else {
        this.resultData = []
      }
    }
  }
}
</script>

<style lang='less' scoped>
.FilterTable{
  padding: 0 5px;
  .FilterBlock{
    padding: 10px;
    border: 1px solid #7bbfea;
    background: aliceblue;
  }
}
.sortBlock{
  height: 200px;
  overflow-y: scroll;
}
.vagueSearchBlock{
  height: 100px;
  padding: 0 5px;
}
</style>



