<template>
  <div class="Contract">
    <h2 class="MarginT_20 MarginB_20"></h2>
    <el-row class="MarginT_20">
      <el-form ref="formContract" :model="formContract" label-width="90px" size="small" style="padding-right: 20px;">
        <el-col :span="6">
          <el-form-item label="">
            <el-date-picker prop="contractDate"
              v-model="formContract.contractDate"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="contractName">
            <el-input v-model="formContract.contractName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="contractNo">
            <el-input v-model="formContract.contractNo" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="constructionTeam">
            <el-select v-model="formContract.constructionTeam" filterable placeholder="" style="width: 100%;">
              <el-option
                v-for="item in constructionTeamList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="payTerm">
            <el-select v-model="formContract.payTerm" placeholder="" style="width: 100%;">
              <el-option
                v-for="item in payTermList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="department">
            <el-select v-model="formContract.department" placeholder="" style="width: 100%;">
              <el-option
                v-for="item in departmentList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="salesman">
            <el-select v-model="formContract.salesman" placeholder="" style="width: 100%;">
              <el-option
                v-for="item in salesmanList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="productionCode">
            <el-select v-model="formContract.productionCode" filterable placeholder="" style="width: 100%;">
              <el-option
                v-for="item in productionNumber"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="affiliatedCompany">
            <el-select v-model="formContract.affiliatedCompany" placeholder="" style="width: 100%;">
              <el-option
                v-for="item in companyeList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="" prop="productionProgress">
            <el-select v-model="formContract.productionProgress" placeholder="" style="width: 100%;">
              <el-option
                v-for="item in progressList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-row class="listColumnTit">
      <el-col :span="1"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
      <el-col :span="2"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
      <el-col :span="3"><span></span></el-col>
    </el-row>
    <!-- list -->
    <el-row class="listTable" v-for="(Record, idx) in formAdd.list" :key="idx">
      <el-col :span="1" style="background:lightsteelblue;"><span>{{idx + 1}}</span></el-col>
      <el-col :span="3">
        <el-select v-model="Record.pcode"  filterable @change="changePCode" size="mini" placeholder="" style="width: 95%;margin: 0 auto;">
          <el-option
            v-for="item in productionCodeList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="3"><span>{{Record.pname}}</span></el-col>
      <el-col :span="3"><span>{{Record.pmodel}}</span></el-col>
      <el-col :span="2"><el-input v-model="Record.pamount" size="mini" clearable style="width: 95%;margin: 0 auto;"></el-input></el-col>
      <el-col :span="3"><el-input v-model="Record.pprice" size="mini" clearable style="width: 95%;margin: 0 auto;"></el-input></el-col>
      <el-col :span="3"><el-input v-model="Record.psum" size="mini" clearable style="width: 95%;margin: 0 auto;"></el-input></el-col>
      <el-col :span="3"><el-input v-model="Record.pnote" size="mini" clearable style="width: 95%;margin: 0 auto;"></el-input></el-col>
      <el-col :span="3"><span>{{Record.punit}}</span></el-col>
    </el-row>
    <section class="MarginT_20">
      <el-button icon="el-icon-plus" @click="addLine"></el-button>
    </section>
  </div>
</template>

<script>
// import { mapState, mapActions } from 'vuex'
// import $ from 'jquery'
export default {
  name: 'Contract',
  data () {
    return {
      formContract: {
        constructionTeam: '',
        department: '',
        payTerm: '',
        salesman: '',
        contractDate: '',
        contractName: '',
        contractNo: '',
        productionCode: '',
        affiliatedCompany: '',
        productionProgress: ''
      },
      formAdd: {
        list: [
          {pcode: 'CODE1', pname: '1', pmodel: '1', pamount: '', pprice: '', psum: '', pnote: '', punit: ''}
        ]
      },
      productionCodeList: [
        {
          value: 'CODE1',
          label: 'CODE1'
        },
        {
          value: 'CODE2',
          label: 'CODE2'
        }
      ],
      productionNumber: [
        {
          value: '1',
          label: '1'
        },
        {
          value: '2',
          label: '2'
        }
      ],
      constructionTeamList: [
        {
          value: '1',
          label: '1'
        }
      ],
      payTermList: [
        {
          value: '1',
          label: '1'
        }
      ],
      departmentList: [
        {
          value: '',
          label: ''
        }
      ],
      salesmanList: [
        {
          value: '',
          label: ''
        }
      ],
      companyeList: [
        {
          value: '',
          label: ''
        }
      ],
      progressList: [
        {
          value: '',
          label: ''
        }
      ]
    }
  },
  computed: {
    // ...mapState({
    // })
  },
  created () {
  },
  methods: {
    addLine () {
      this.formAdd.list.push({pcode: '', pname: '', pmodel: '', pamount: '', pprice: '', psum: '', pnote: '', punit: ''})
    },
    changePCode (val) {
      console.log(val)
    }
  }
}
</script>

<style lang='less' scoped>
  .listColumnTit{
    background:lightsteelblue;
    border-bottom: 1px solid #eee;
    .el-col{
      &:not(:last-child){
        border-right: 1px solid #eee;
      }
       span{
        height: 42px;
        line-height: 42px;
        display: block;
      }
    }
  }
  .listTable{
    border-bottom: 1px solid #eee;
    .el-col{
      height: 42px;
      line-height: 42px;
      &:not(:last-child){
        border-right: 1px solid #eee;
      }
      span{
        font-size: 14px;
      }
    }
  }
</style>



